.App {
  text-align: center;
}
#ul {
  display: flex;
}
#ul li {
  border: 2px solid navy;
  border-radius: 5px;
  margin: 5px;
  padding: 15px;
  list-style: none;
  text-decoration: none;
  cursor: pointer;
  background-color: orange;
  color: black;
  font-size: large;
  font-weight: bold;
  border-color: orange;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
